
.wrapper[data-v-4f3a213b] {
    background: #fff;
    bottom: 0;
    left: 0;
    overflow: hidden;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 99999;
}
.showbox[data-v-4f3a213b] {
  transform: translatey(-25%);
  -webkit-transform: translatey(-25%);
  position: absolute;
  top: 50%;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 5%;
}
.loader[data-v-4f3a213b] {
  position: relative;
  margin: 0 auto;
  width: 100px;
}
.loader[data-v-4f3a213b]:before {
  content: '';
  display: block;
  padding-top: 100%;
}
.circular[data-v-4f3a213b] {
  -webkit-animation: rotate 2s linear infinite;
  animation: rotate 2s linear infinite;
  height: 100%;
  -webkit-transform-origin: center center;
  transform-origin: center center;
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}
.path[data-v-4f3a213b] {
  stroke-dasharray: 1, 200;
  stroke-dashoffset: 0;
  -webkit-animation: dash 1.5s ease-in-out infinite, color 6s ease-in-out infinite;
  animation: dash 1.5s ease-in-out infinite, color 6s ease-in-out infinite;
  stroke-linecap: round;
}
@-webkit-keyframes rotate {
100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
}
}
@keyframes rotate {
100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
}
}
@-webkit-keyframes dash {
0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
}
50% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -35px;
}
100% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -124px;
}
}
@keyframes dash {
0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
}
50% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -35px;
}
100% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -124px;
}
}
@-webkit-keyframes color {
100%,
  0% {
    stroke: #d62d20;
}
40% {
    stroke: #0057e7;
}
66% {
    stroke: #008744;
}
80%,
  90% {
    stroke: #ffa700;
}
}
@keyframes color {
100%,
  0% {
    stroke: #d62d20;
}
40% {
    stroke: #0057e7;
}
66% {
    stroke: #008744;
}
80%,
  90% {
    stroke: #ffa700;
}
}



