
#statistic .number {
  position: relative;
  margin-top: 44px;
  border-bottom: 1px solid #bbbbbb;
  background: #26A2FF;
  height: 200px;
}
#statistic .number span {
    font-weight: bold;
    color: #26A2FF;
    font-size: 20px;
}
#statistic .number .isSign {
    position: relative;
    z-index: 2;
    top: 20px;
    height: 100px;
    width: 100px;
    border-radius: 50%;
    background: #fff;
    border: 8px solid #0086FF;
    margin: 0 auto 50px auto;
    text-align: center;
    font-size: 16px;
}
#statistic .number .isSign .isSign-top {
      margin-top: 10px;
}
#statistic .number .isSign .isSign-bottom {
      margin-top: 20px;
}
#statistic .number .total {
    position: absolute;
    display: inline-block;
    right: 20px;
    bottom: 20px;
    z-index: 5;
    color: #fff;
}
#statistic .lists {
  background: #fff;
  border-bottom: 1px solid #bbbbbb;
}
#statistic .lists .list-th {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 50px;
    background: #38ADFF;
    color: #fff;
}
#statistic .lists .list-th span {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      line-height: 50px;
      text-align: center;
}
#statistic .lists .list-member {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 45px;
    line-height: 45px;
    font-size: 16px;
}
#statistic .lists .list-member span {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      text-align: center;
}
#statistic .lists .list-member .red {
      color: red;
}
#statistic .lists .list-member .green {
      color: green;
}



