
.downloadDetail {
  margin-top: 45px;
}
.downloadDetail .main-wrapper {
    background: white;
    font-size: 13px;
}
.downloadDetail .main-wrapper .extension {
      text-align: center;
      padding-top: 35px;
}
.downloadDetail .main-wrapper .extension span {
        display: inline-block;
        width: 75px;
        height: 75px;
        line-height: 75px;
        background: #33B670;
        color: white;
        text-align: center;
        font-size: 26px;
}
.downloadDetail .main-wrapper .filename {
      text-align: center;
      font-size: 18px;
      padding: 10px 20px 0 20px;
}
.downloadDetail .main-wrapper .length {
      text-align: center;
      color: #8FA8B5;
      padding-top: 10px;
}
.downloadDetail .main-wrapper .upload {
      padding: 15px;
}
.downloadDetail .main-wrapper .downTimes {
      padding: 0 0 15px 15px;
}
.downloadDetail .list-wrapper {
    margin-top: 30px;
    background: white;
}
.downloadDetail .list-wrapper .list-title {
      padding-left: 20px;
      height: 45px;
      line-height: 45px;
      border-bottom: 1px solid #E5E9F2;
}
.downloadDetail .list-wrapper .list-member span {
      line-height: 45px;
      text-align: center;
      padding-left: 30px;
}
.downloadDetail .list-wrapper .list-member .downloadTime {
      font-size: 13px;
      color: #8FA8B5;
      float: right;
      padding-right: 30px;
}



