
#tabbar {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 87px;
  background-color: #f8f8f8;
  border-top: 1px solid #8492A6;
  z-index: 50;
}
#tabbar .item {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}
#tabbar .item .icon {
      text-align: center;
      color: #26A2FF;
      font-size: 14px;
      height: 45px;
}
#tabbar .item .icon i {
        padding-top: 15px;
}
#tabbar .item .item-name {
      text-align: center;
      color: #26A2FF;
      font-size: 14px;
      height: 32px;
      line-height: 32px;
}



