
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.25s ease-out;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
#register {
  margin-top: 45px;
}
#register .head {
    height: 40px;
    line-height: 40px;
}
#register .head span {
      height: 40px;
      margin-right: 10px;
      font-size: 14px;
}
#register .head span i {
        margin: 10px;
        color: #38ADFF;
}
#register .head .hour {
      float: right;
}
#register #allmap {
    width: 100%;
    height: 35vh;
}
#register .info {
    padding: 10px 10px 10px 16px;
    line-height: 25px;
    background: #fff;
    font-size: 18px;
    border-bottom: 1px solid #ddd;
}
#register .register-box {
    border: 1px solid #ddd;
    background: #38ADFF;
    height: 141px;
    width: 141px;
    margin: 23px auto;
    border-radius: 50%;
    text-align: center;
    font-size: 15px;
}
#register .register-box .register-box-content i {
      color: #fff;
      padding-bottom: 10px;
}
#register .register-box .register-box-content .click {
      margin-top: 25px;
      font-size: 18px;
      color: #fff;
}
#register .register-box .register-box-content .isClick {
      margin-top: 55px;
      color: #fff;
      font-weight: bold;
      font-size: 20px;
}
#register .register-box .register-detail {
      margin-top: 20px;
}
#register .register-box .register-detail p {
        font-size: 13px;
        margin: 5px 20px;
}
#register .signIn {
    font-size: 16px;
    background: #fff;
}
#register .signIn p {
      padding-left: 10px;
      border-bottom: 1px solid #EFF2F7;
}
#register .signIn p span {
        color: #26A2FF;
}
#register .signIn .order {
      height: 55px;
      line-height: 55px;
}
#register .signIn .time,
    #register .signIn .location {
      padding: 10px;
      line-height: 25px;
}
#register .chart {
    overflow: hidden;
}
#register .chart .number {
      position: absolute;
      width: 50%;
      text-align: center;
      top: 33%;
      left: 25%;
      font-size: 22px;
      color: #26A2FF;
}
#register .chart .text {
      position: absolute;
      width: 50%;
      text-align: center;
      top: 47%;
      left: 25%;
      font-size: 13px;
}
#register .chart .data {
      margin: 20px auto;
      width: 90%;
      height: 30px;
      background: #26A2FF;
      border-radius: 8px;
      text-align: center;
      line-height: 30px;
      color: #fff;
      font-size: 14px;
}



