
#cell {
  color: black;
  text-align: left;
  background-color: #fff;
  border-top: 1px solid transparent;
  border-bottom: 1px solid transparent;
  margin-top: 21px;
}
#cell .title-wrapper {
    width: 100%;
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    font-weight: bold;
    font-size: 18px;
}
#cell .title-wrapper .title {
      padding: 10px 0 10px 20px;
      -webkit-box-flex: 1;
          -ms-flex-positive: 1;
              flex-grow: 1;
}
#cell .title-wrapper .title-badge {
      -ms-flex-negative: 0;
          flex-shrink: 0;
      -webkit-box-flex: 0;
          -ms-flex-positive: 0;
              flex-grow: 0;
      width: 62px;
      height: 30px;
      font-size: 14px;
      margin: 10px;
}
#cell .title-wrapper .title-badge .title-status {
        line-height: 30px;
        color: white;
}
#cell .content-wrapper {
    margin: 0 20px 9px 20px;
    font-size: 13px;
}
#cell .content-wrapper p {
      padding: 5px 0;
}



