
.extra {
  font-size: 16px;
  padding-bottom: 100px;
}
.extra .wrapper {
    background: #fff;
    margin-bottom: 25px;
}
.extra .wrapper .list-title {
      padding-left: 15px;
      color: #26A2FF;
      height: 45px;
      line-height: 45px;
}
.extra .wrapper .list-th {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      margin-bottom: 10px;
}
.extra .wrapper .list-th span {
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
        height: 30px;
        line-height: 30px;
        text-align: center;
}
.extra .wrapper .list-member {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      min-height: 30px;
      padding-bottom: 10px;
      color: #8492A6;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.extra .wrapper .list-member span {
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
        text-align: center;
        padding: 0 10px;
}



