
.cbp_tmtimeline {
  list-style: none;
  position: relative;
  background: white;
  /* The line */
}
.cbp_tmtimeline:before {
    content: '';
    position: absolute;
    top: 18px;
    bottom: 0;
    width: 1px;
    background: #20A0FF;
    left: 87px;
}
.cbp_tmtimeline > li {
    position: relative;
    /* The date/time */
    /* Right content */
    /* The icons */
}
.cbp_tmtimeline > li .cbp_tmtime {
      display: block;
      padding-right: 100px;
      position: absolute;
}
.cbp_tmtimeline > li .cbp_tmtime span {
        padding-left: 15px;
        display: block;
        text-align: center;
}
.cbp_tmtimeline > li .cbp_tmtime span.date {
          font-size: 14px;
          color: #bdd0db;
}
.cbp_tmtimeline > li .cbp_tmtime span.time {
          font-size: 18px;
          color: #3594cb;
}
.cbp_tmtimeline > li .cbp_tmtime span.day {
          font-size: 14px;
          color: #bdd0db;
}
.cbp_tmtimeline > li .cbp_tmlabel {
      margin: 0 15px 25px 110px;
      background: #F5FAFD;
      border: 1px solid #26A2FF;
      color: #000;
      font-weight: 300;
      position: relative;
      border-radius: 10px;
      /* The triangle */
}
.cbp_tmtimeline > li .cbp_tmlabel:before {
        right: 100%;
        border: solid transparent;
        content: " ";
        height: 0;
        width: 0;
        position: absolute;
        pointer-events: none;
        border-right-color: #F5FAFD;
        border-width: 10px;
        top: 12px;
        left: -19px;
        z-index: 1;
}
.cbp_tmtimeline > li .cbp_tmlabel:after {
        right: 100%;
        border: solid transparent;
        content: " ";
        height: 0;
        width: 0;
        position: absolute;
        pointer-events: none;
        border-right-color: #26A2FF;
        border-width: 11px;
        top: 11px;
}
.cbp_tmtimeline > li .cbp_tmlabel .title {
        padding: 5px 0 5px 0px;
        font-size: 15px;
        color: #26A2FF;
}
.cbp_tmtimeline > li .cbp_tmlabel .speaker {
        font-size: 15px;
}
.cbp_tmtimeline > li .cbp_tmlabel p {
        font-size: 15px;
        margin: 5px 10px 5px 10px;
}
.cbp_tmtimeline > li .cbp_tmlabel p.content {
          position: relative;
          padding-top: 5px;
          padding-bottom: 5px;
          line-height: 15px;
          max-height: 45px;
          overflow: hidden;
}
.cbp_tmtimeline > li .cbp_tmlabel .more {
        font-size: 14px;
        /*position: relative;*/
        margin: 0 10px 8px 10px;
        color: #8492A6;
        padding-top: 5px;
        border-top: 1px solid rgba(153, 169, 191, 0.5);
}
.cbp_tmtimeline > li .cbp_tmlabel .more span {
          float: right;
}
.cbp_tmtimeline > li .cbp_tmicon {
      width: 10px;
      height: 10px;
      font-size: 16px;
      line-height: 40px;
      -webkit-font-smoothing: antialiased;
      position: absolute;
      color: #fff;
      background: white;
      border-radius: 50%;
      box-shadow: 0 0 0 2px #20A0FF;
      text-align: center;
      left: 107px;
      top: 0;
      margin: 18px 0 0 -25px;
}



