
.personalData {
    font-size: 10px;
    margin-top: 50px;
}
.personalData .header {
    background: white;
    font-size: 0;
    padding: 12px 12px 12px 12px;
    border-top: 1px solid #8492A6;
    border-bottom: 1px solid #8492A6;
}
.personalData .avatar {
    display: inline-block;
}
.personalData .content {
    width: 200px;
    display: inline-block;
    font-size: 14px;
    margin-left: 16px;
}
.personalData .content .name {
    font-weight: bold;
}
.personalData .content .signature {
    font-size: 14px;
    padding-top: 5px;
}
.personalData .main {
    margin: 15px 0;
    border-top: 1px solid #8492A6;
    border-bottom: 1px solid #8492A6;
    background: white;
}
.personalData .main .main-cell {
    padding: 10px 20px;
    font-size: 14px;
}
hr {
    height: 1px;
    border: none;
    border-top: 1px solid #8492A6;
    width: 90%;
    margin: 0px auto;
}
.personalData .switch-wrapper {
    padding: 10px 20px;
    background: white;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.personalData .switch-wrapper .switch-title {
    font-size: 14px;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}
.switch-intro {
    color: #8492A6;
    padding-left: 20px;
    margin: 0px;
}



