
#registered {
  margin-top: 45px;
}
#registered .info {
    height: 35px;
    padding-left: 10px;
    font-size: 14px;
    line-height: 35px;
}
#registered .signIn {
    font-size: 16px;
    background: #fff;
}
#registered .signIn p {
      padding-left: 10px;
      border-bottom: 1px solid #EFF2F7;
}
#registered .signIn p span {
        color: #26A2FF;
}
#registered .signIn .order {
      height: 65px;
      line-height: 66px;
}
#registered .signIn .time,
    #registered .signIn .location {
      padding: 10px;
      line-height: 25px;
}
#registered .chart {
    overflow: hidden;
}
#registered .chart .number {
      position: absolute;
      width: 50%;
      text-align: center;
      top: 33%;
      left: 25%;
      font-size: 22px;
      color: #26A2FF;
}
#registered .chart .text {
      position: absolute;
      width: 50%;
      text-align: center;
      top: 47%;
      left: 25%;
      font-size: 13px;
}
#registered .chart .data {
      margin: 20px auto;
      width: 90%;
      height: 30px;
      background: #26A2FF;
      border-radius: 8px;
      text-align: center;
      line-height: 30px;
      color: #fff;
      font-size: 14px;
}



