
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.25s ease-out;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.app .top-fixed {
  position: fixed;
  top: 45px;
  width: 100%;
  z-index: 50;
}
.app .top-fixed .select-wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 54px;
    background: white;
    border-bottom: 1px solid #bbbbbb;
    font-size: 16px;
    color: #8492A6;
}
.app .top-fixed .select-wrapper .select {
      display: inline-block;
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      text-align: center;
      height: 44px;
      padding: 5px 0;
}
.app .top-fixed .select-wrapper .select span {
        line-height: 45px;
}
.app .top-fixed .select-wrapper .active {
      border-bottom: 3px solid #26A2FF;
      color: #26A2FF;
}
.app .content {
  margin-top: 120px;
}
.app .filter-wrapper {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 50;
  background: rgba(240, 240, 242, 0.5);
}
.app .filter-wrapper .filter {
    position: relative;
    width: 284px;
    height: 160px;
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    border: 1px solid #BBBBBB;
    border-radius: 5px;
    background: white;
    font-size: 0;
}
.app .filter-wrapper .filter .filter-title {
      height: 40px;
      line-height: 40px;
      text-align: center;
      font-weight: bold;
      font-size: 16px;
}
.app .filter-wrapper .filter .filter-main {
      margin: 0 28px;
}
.app .filter-wrapper .filter .filter-main a {
        display: inline-block;
        margin-top: 9px;
        height: 19px;
        width: 74px;
        text-align: center;
        line-height: 21px;
        border: 1px solid #BBBBBB;
        font-size: 12px;
}
.app .filter-wrapper .filter .filter-main .active {
        color: white;
        background: #FF7043;
}
.app .filter-wrapper .filter .filter-bottom {
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 45px;
}
.app .filter-wrapper .filter .filter-bottom span {
        display: inline-block;
        width: 140px;
        text-align: center;
        line-height: 43px;
        border: 1px solid #BBBBBB;
        font-size: 16px;
        font-weight: bold;
}
.app .filter-wrapper .filter .filter-bottom span.confirm {
          color: #1C74D9;
}
.app .filter-wrapper .filter .filter-bottom span.cancer {
          color: #888888;
}
:link {
  text-decoration: none;
}



