
.meetingAgenda .agenda-wrapper {
  margin-top: 45px;
  background: #fff;
  font-size: 16px;
}
.meetingAgenda .agenda-wrapper .title-wrapper {
    padding: 15px 25px;
    font-weight: bold;
    font-size: 18px;
}
.meetingAgenda .agenda-wrapper .title-wrapper .title {
      margin-left: 12px;
}
.meetingAgenda .agenda-wrapper .nameTime {
    padding: 0 0 15px 25px;
    position: relative;
    color: #8FA8B5;
    border-bottom: 1px solid #EFF2F7;
}
.meetingAgenda .agenda-wrapper .nameTime .time {
      margin-left: 35px;
}
.meetingAgenda .agenda-wrapper .content {
    text-indent: 2em;
    padding: 20px 25px;
    line-height: 25px;
}



