
#member-cell {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #fff;
  padding: 10px 0;
}
#member-cell .avatar {
    height: 45px;
    margin: 5px 5px 5px 15px;
}
#member-cell .avatar .image {
      width: 45px;
      height: 45px;
      border-radius: 50%;
      -webkit-box-flex: 0;
          -ms-flex: 0 0 40px;
              flex: 0 0 40px;
}
#member-cell .avatar .image .image-name {
        color: white;
        font-size: 14px;
        display: inline-block;
        width: 45px;
        line-height: 45px;
        text-align: center;
}
#member-cell .avatar .host {
      background-color: #10DDDD;
}
#member-cell .avatar .speaker {
      background-color: #D2A8F5;
}
#member-cell .avatar .audience {
      background-color: #21BF07;
}
#member-cell .name {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    height: 45px;
    line-height: 45px;
    padding: 5px 5px 5px 10px;
    font-size: 16px;
}



